:root {
  --blue: #4f2ee8;
  --gold: #eac645;
  --dark: #111;
  --white: #fff;
  --black: #202024;
}

.list {
  list-style: none;
}

a {
  color: #000;
  text-decoration: none;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

.switch {
  width: 40px;
  height: 20px;
  margin-left: 144px;
  display: inline-block;
  position: relative;
}

.switch input {
  display: none;
}

.slider {
  cursor: pointer;
  background: linear-gradient(180deg, var(--blue) 0%, #dcdcdc 100%);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 18px;
  width: 18px;
  background-color: #fff;
  transition: all .4s;
  position: absolute;
  bottom: 2px;
  left: 2px;
}

input:checked + .slider, input:focus + .slider {
  background: linear-gradient(180deg, var(--blue) 0%, #686868 100%);
}

input:checked + .slider:before {
  transform: translateX(16px);
}

.slider.round {
  border-radius: 18px;
}

.slider.round:before {
  border-radius: 50%;
}

.header-section {
  border-bottom: 1px solid var(--footer-header-color);
  box-shadow: 0 2px 1px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 1px 6px rgba(46, 47, 66, .08);
}

.logo {
  letter-spacing: .03em;
  text-transform: uppercase;
  color: var(--second-color);
  padding-top: 24px;
  padding-bottom: 24px;
  font-family: Raleway, sans-serif;
  font-size: 18px;
  font-weight: 800;
  line-height: 1.17;
}

.logo span {
  color: var(--logo-button-color);
}

@media screen and (max-width: 767px) {
  .menu-list, .contact-list, .page-nav, .wellcome-div {
    display: none;
  }

  .page-header-wrapper {
    justify-content: space-between;
    align-items: center;
    display: flex;
  }

  .mobile-menu-open-btn {
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0);
    background-image: url("open-menu-btn.2a33c908.svg");
    background-repeat: no-repeat;
    border: none;
    margin-left: 10px;
  }

  .mobile-menu-icon {
    stroke: var(--second-color);
  }

  .mobile-menu-container-wrapper {
    width: 100%;
    height: 100vh;
    background-color: var(--white-color);
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    position: fixed;
    top: 0;
    left: 0;
  }

  .mobile-menu-container-wrapper.is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
  }

  .mobile-menu-container {
    width: 100%;
    height: 100%;
    flex-direction: column;
    justify-content: space-between;
    padding: 80px 60px 40px 40px;
    display: flex;
    position: relative;
    overflow: auto;
  }

  .mobile-menu-close-btn {
    width: 24px;
    height: 24px;
    width: 24px;
    height: 24px;
    cursor: pointer;
    background-color: var(--footer-color);
    border: 1px solid rgba(0, 0, 0, .1);
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
    display: flex;
    position: absolute;
    top: 40px;
    right: 40px;
  }

  .mobile-menu-close-btn:hover, .mobile-menu-close-btn:focus {
    background-color: var(--gold);
  }

  .mobile-menu-list-link {
    flex-direction: column;
    gap: 40px;
    display: flex;
  }

  .link-mobile-menu {
    letter-spacing: .02em;
    text-transform: capitalize;
    color: var(--second-color);
    font-size: 36px;
    font-weight: 700;
    line-height: 1.11;
  }

  .active.link-mobile-menu {
    color: var(--logo-button-color);
  }

  .mobile-menu-contacts-list {
    flex-direction: column;
    gap: 40px;
    margin-top: auto;
    margin-bottom: 48px;
    display: flex;
  }

  .mobile-menu-tel {
    letter-spacing: .02em;
