  .mobile-menu-mail {
    letter-spacing: .02em;
    color: var(--contact-description-color);
    font-size: 20px;
    font-weight: 500;
    line-height: 1.2;
  }

  .mobile-menu-socialmedia-list {
    gap: 48px;
    display: flex;
  }

  .mobile-menu-socialmedia-item {
    width: 40px;
    height: 40px;
    background-color: var(--logo-button-color);
    border-radius: 50%;
  }

  .mobile-menu-socialmedia-link {
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .mobile-menu-icon-instagram {
    width: 24px;
    height: 24px;
  }
