@media screen and (min-width: 768px) {
  .mobile-menu-container-wrapper, .mobile-menu-open-btn {
    display: none;
  }

  .menu-list {
    align-items: center;
    display: flex;
  }

  .menu-item:not(:last-child) {
    margin-right: 40px;
  }

  .menu-link {
    letter-spacing: .02em;
    color: var(--second-color);
    padding-top: 24px;
    padding-bottom: 26px;
    font-family: Roboto;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.5;
    transition: color .25s cubic-bezier(.4, 0, .2, 1);
  }

  .current {
    color: var(--logo-button-color);
    z-index: 5;
    position: relative;
  }

  .current:after {
    content: "";
    width: 120%;
    height: 20px;
    background-color: var(--gold);
    opacity: 1;
    z-index: 1;
    border-radius: 36px;
    display: block;
    position: absolute;
    bottom: 25px;
    left: -5px;
  }
}

